				"html": "<p>This week, while assessing current life, I realized with procedural changes I had room to grow time spent doing what makes me happy. Growing happiness and optimizing my time are the same—if I optimize my time, I have more time to do things that make me happy. I try assess how happy I am and what is not making me happy often. </p>\n<p>I know that its time to make my happiness level assessment when I have unhappy. Unhappiness is a very abstract thing. It is not factual or logical. It is good to have unhappy feelings. Unhappy feelings stress what is not working or what needs to be worked on. In the past, I would have feelings of unhappiness but I would not think about why I was unhappy logically. This caused me to feel unhappy longer. It caused me feel powerless to change my unhappiness. I was reliant on unhappy situations to end by themselves or change outside of the scope of my control.</p>\n<p>4 years ago, I realized that if I thought through why I was feeling unhappy—really breaking down what was not going well—that I was able to either accepts the problems or make a conscious effort to fix it.</p>\n<p>There are steps I follow and questions I ask to address my unhappy feelings. </p>\n<ul>\n<li>\n<p>I address potential physical issues</p>\n<ul>\n<li>Am I having physical pains that might be associated with my feelings?</li>\n<li>Am I having physical ailments that could be leading me to have unhappy feelings?</li>\n<li>How’s my sleep schedule been?</li>\n</ul>\n</li>\n<li>\n<p>What is a realistic worst case scenario of my unhappy feelings? </p>\n<ul>\n<li>What are my options if that scenario happens?</li>\n<li>What is the actual probability of that scenario happening?</li>\n</ul>\n</li>\n<li>What is a realistic time line to a resolution of my unhappy feelings?</li>\n<li>Who can I share theses feelings with that will be respectful yet honest?</li>\n</ul>\n<p>Digging into the worst case scenario, for me, makes it a problem to be solved rather than something that is an abstract unhappy feeling. I am able to think of alternate options, possible benefits and every time so far—I’ve realized that the worst case scenario is not very realistic. </p>\n<p>Last week, I was having some unhappy feelings. I thought about why. I had suffered an ankle injury. I had projects that seemed bleek. My team seemed stressed. Taking the time to think about these things relaxed me. Rather than thinking about my sucky situation, I began to think about solutions. I talked to a few people as soon as I could about it. Together, we assessed my feelings and then solutions. The result, so far, has been more productivity which has lead to more fun for me. </p>\n<html><head></head><body><figure>\n  \n</figure></body></html>",
